
*{
  margin: 0px;
  padding: 0px;
  font-family: 'Fasthand', cursive;
}

.App {
  text-align: center;
}

body {
  background: #075487;
  background-image: linear-gradient(135deg, #f7d9ea, #bae2fb);
  background-image: 
        linear-gradient(135deg,
        #fbbad7 80%, #ece7ea 15%, #d6ec33 95%);
        background-size: 100% 200%;
        transition: all .25s ease;
  background-size: cover;
}

.container {
  width: calc(80% - 60px);
  margin: 8% 10%;
  box-shadow: 0px 0px 10px #a79d9d;
  height: 60vh;
  background: #6fea8c;
  padding: 30px;
  border-radius: 10px;
}

.container h3 {
  color: #025b61;
  font-size: 28px;
  text-shadow: 0px 0px 2px #bae2fb;
  color: #4c356e;
  
}

.container .search-text {
  font-weight: bold;
  padding: 5px;
  font-size: 24px;
  margin: 20px 0px;
  color: black;
}

.display_links a {
  font-size: 20px;
  text-decoration: none;
  color: #d3c9f5;
}